.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.btn {
  width:90%;
  margin:0 auto;
  background: #d16b6b;
  background-image: -webkit-linear-gradient(top, #d16b6b, #a34b4b);
  background-image: -o-linear-gradient(top, #d16b6b, #a34b4b);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#d16b6b), to(#a34b4b));
  background-image: linear-gradient(to bottom, #d16b6b, #a34b4b);
  font-family: Arial;
  color: #ffffff;
  font-size: 95%;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
}

.btn:hover {
  background: #ffd9d9;
  background-image: -webkit-linear-gradient(top, #ffd9d9, #b58080);
  background-image: -o-linear-gradient(top, #ffd9d9, #b58080);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ffd9d9), to(#b58080));
  background-image: linear-gradient(to bottom, #ffd9d9, #b58080);
  text-decoration: none;
}

.status-box{
  padding: 1px;
}

.ToolTip{
  text-align:left;
}

.combatLog{
  background:black;
  width:30%;
  /*height:100%;*/
}
#game{
  display:-ms-flexbox;
  display:flex;
}

.image-for-cell{
  height:15px;
  width:auto;
}

.dungeonKey{
  padding-top:5em;
}
.dungeonKey ul{
  list-style-type:none;
  }

/*  styles for cells */
.player{
  background:#6F4D8F;
  height:12px;
  width:12px;

  border:2px black solid;
}
.enemy{
  background:darkred;
  height:12px;
  /* margin-top:2px; */
  width:12px;
  border:2px grey solid;
}
.health{
  background:#714C8F;
  border:solid #5E7914 4px;
  /* margin:5px; */
  padding:4px;
  /* height:15px;
  width:15px; */
}
.portal{
  background:#A15089;
  font-size:9pt;
  height:16px;
  width:16px;
}
.weapon{
  background:#9476AB;
  height:16px;
  width:16px;
}
.wall{
  background:#803615;
  padding-left:4px;
  color:black;
  height:13px;
  width:6px;
  border:2px solid black;
  overflow: hidden;
}
.floor{
  background:#515644;
  height:16px;
  width:16px;
}

.boss{
  background:black;
  font-size:6pt;
  color:red;
  height:12px;
  width:12px;
  border:2px red solid;
}



